<template lang="html">
  <div class="session">
    <div class="boxNotif" v-if="notifRes">
       <v-snackbar
        v-model="snackbar"
        :color="notifColor"
        :timeout="timeout"
        top
      >
        {{ notifResMessage.status }}
        <v-btn
          dark
          flat
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </v-snackbar>
    </div>
    <div class="session--left">
      <UpcomingSessionFilter @clicked="onClickChild" />
      <keep-alive>
        <SessionHistory v-if="sessionTabs === 'history'" />
        <!-- <UpcomingSessions :startDate="startDate" :endDate="endDate" /> -->
        <UpcomingList v-if="sessionTabs === 'upcoming'"/>
      </keep-alive>
    </div>

    <div class="session--right coach__view">
      <UpcomingStudentDetail />
    </div>
    <UpcomingProgressReport @clicked="snackbarVal" />
    <SessionHistoryModalOpentok v-if="CheckToogle2"/> 
    <SessionHistoryModalAgora  v-if="CheckToogle3"/>
  </div>
</template>

<script>
import UpcomingSessionFilter from '../../coach-components/sessions/upcoming/UpcomingSessionFilter'
import UpcomingStudentDetail from '../../coach-components/sessions/upcoming/UpcomingStudentDetail'
import UpcomingList from '../../coach-components/sessions/upcoming/UpcomingList'
import SessionHistory from '../../coach-components/sessions/history/SessionHistory.vue'
import SessionHistoryModalOpentok from '../../coach-components/sessions/history/SessionHistoryModalOpentok.vue'
import UpcomingProgressReport from '../../coach-components/sessions/upcoming/UpcomingProgressReport'
import UpcomingNotif from '../../coach-components/sessions/upcoming/UpcomingNotif'
import SessionHistoryModalAgora from '../../student-components/sessions/history/SessionHistoryModalAgora'

export default {
  components: {
    SessionHistory,
    SessionHistoryModalOpentok,
    UpcomingSessionFilter,
    UpcomingStudentDetail,
    UpcomingList,
    UpcomingProgressReport,
    UpcomingNotif,
    SessionHistoryModalAgora
  },
  data () {
    return {
      showDate: false,
      startDate: null,
      endDate: null,
      modal: false,
      snackbar: false,
      timeout: 3000,
      sessionTabs: 'upcoming'
    }
  },
  created () {
    this.$store.dispatch('GET_UPCOMING_SESSIONS')
    this.$store.dispatch('GET_PROFILE_SESSION_HISTORY')
  },
  computed: {
    CheckToogle2(){
      return this.$store.state.showDialogTwo
    },
    CheckToogle3(){
      return this.$store.state.showDialogThree
    },
    notifRes() {
      return this.$store.state.rescheduleResponse !== null
    },
    notifResMessage() {
      return this.$store.state.rescheduleResponse
      // console.log(resCoins);
    },
    notifColor() {
      const resStatus = this.$store.state.rescheduleResponse

      if (resStatus.status == "success") {
        return "success"
      } else {
        return "error"
      }
    }
  },
  methods: {
    onClickChild (value) {
      this.sessionTabs = value // someValue
    },
    snackbarVal (value) {
      return this.snackbar = value
      console.log(value);
    }
  },
  beforeDestroy() {
    this.snackbar = false
  }
}
</script>

<style lang="scss">
  @import '../sessions/Session.scss'
</style>



