<template lang="html">
  <v-dialog width="1020" v-model="showDialog" persistent>
    <v-card style="background-color:#fff;border-radius: 3px;">
      <v-btn icon light @click="changeDialogState">
        <v-icon>close</v-icon>
      </v-btn>
      <div class="upcoming__report">
        <div class="student__progress__content" v-if="handleWhichStudent == 'b2i'">
          <div class="flex--column width--50perc">
            <StudyLevel />
            <ProgressBar />
          </div>
          <CertificatePlan />
        </div>
        <div class="" v-if="handleWhichStudent == 'b2c'">
          <div>
            <div class="" v-if="!handleWeeklyProgress">
              waiting..
            </div>
            <div class="study__bottom live--sess" v-if="handleWeeklyProgress">
              <CertCompletion />
              <PointsGoal :getWeeklyProgress="getWeeklyProgress" />
              <ProgressGoal />
              <ComprehensionGoal :getWeeklyProgress="getWeeklyProgress" />
              <PronunciationGoal :getWeeklyProgress="getWeeklyProgress" />
              <ListeningGoal :getWeeklyProgress="getWeeklyProgress" />
              <SpeakingGoal :getWeeklyProgress="getWeeklyProgress" />
            </div>
          </div>
        </div>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
  import StudyLevel from '../../../student-components/studentProgressReport/StudyLevel'
  import ProgressBar from '../../../student-components/studentProgressReport/ProgressBar'
  import CertificatePlan from '../../../student-components/studentProgressReport/CertificatePlan'
  import CertCompletion from '../../../student-components/study-progress-report/cert-completion/CertCompletionWrapper'
  import ProgressGoal from '../../../student-components/study-progress-report/progress-goal/ProgressGoalWrapper'
  import PointsGoal from '../../../student-components/study-progress-report/bar-chart-point/PointsGoal'
  import ComprehensionGoal from '../../../student-components/study-progress-report/bar-chart-point/ComprehensionGoal'
  import PronunciationGoal from '../../../student-components/study-progress-report/bar-chart-point/PronunciationGoal'
  import ListeningGoal from '../../../student-components/study-progress-report/bar-chart-point/ListeningGoal'
  import SpeakingGoal from '../../../student-components/study-progress-report/bar-chart-point/SpeakingGoal'

export default {
  components: {
    StudyLevel,
    ProgressBar,
    CertificatePlan,
    CertCompletion,
    ProgressGoal,
    PointsGoal,
    ComprehensionGoal,
    PronunciationGoal,
    ListeningGoal,
    SpeakingGoal
  },
  data () {
    return {

    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
    //FOR LOTTIE
    handleAnimation: function (anim) {
       this.anim = anim;
    },
    handleWhichStudent () {
      const data = this.$store.state.stateB2student

      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    getWeeklyProgress () {
      // const data = this.$store.state.stateWeeklyProgress
      //
      // if (data == null) {
      //   return 0
      // } else {
      //   return data
      // }
      return this.$store.state.stateWeeklyProgress
    },
    handleWeeklyProgress () {
      if (this.getWeeklyProgress == null) {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    // ACTION FOR CANCEL BUTTON
    changeDialogState () {
      this.$store.commit('toggleDialogFalse')
    },
  },
  watch: {

  }
}
</script>

<style lang="scss">
  @import '../../../views/studentProgressReport/StudentProgressReport.scss';
  @import '../../../views/study-progress-report/StudyProgressReport.scss';
  .upcoming__report {
    padding: 10px 20px;
  }
</style>



